<template>
  <div id="shop">
      <header-logo />
      <header class="topheader">
          <div class="bgimg">
                <img src="../../../assets/picture/shopbanner1.jpg" alt="">
            </div>
          <div class="content">
                <div class="title1">
                    <h1>REFRESH YOURSELF </h1>
                    <h1>WITH OUR FRUITS</h1>
                </div>
                <div class="title2">
                    <p>Feast on a wide variety of fresh fruits all over the world. We offer</p>
                    <p>high quality products that will surely satisfy your appetite! </p>
                </div>
                <div class="down" @click="scrollDown">
                    <img src="../../../assets/picture/down.gif" alt="">
                </div>
            </div>
      </header>
      <div style="height:32px;"></div>
      <div class="allshop">
            <p class="alltitle">Shop All</p>
            <div class="sordbyBox">
                <select  id="sortBy" class="sortBy">
                    <option  value="default">Sort by</option>
                    <option  value="newest">Newest</option>
                    <option  value="price_ascending">Price (low to high)</option>
                    <option  value="price_descending">Price (high to low)</option>
                    <option  value="name_ascending">Name A-Z</option>
                    <option  value="name_descending">Name Z-A</option>
                </select>
            </div>
            <div class="shopul">
                <div class="shopli" v-for="item in 10" :key="item">
                    <div class="img">
                        <router-link to="/m/shop/detail">
                            <img src="../../../assets/picture/shopimg.png" />
                        </router-link>
                    </div>
                    <div class="details">
                        <div class="title" >Fruit Gift Box</div>
                        <div class="split" ></div>
                        <div class="price">₱100.00</div>
                        <router-link to="/" class="btn">Add to Cart</router-link>
                    </div>
                </div>
            </div>
            <div class="loadmore">Load More</div>
      </div>
      <div style="height: 25px;"></div>
  </div>
</template>

<script>
import HeaderLogo from '../Layout/HeaderLogo'
import { mapMutations } from "vuex";
export default {
    data() {
        return {
            dialogVisible: false,
            count:1
        }
    },
    components:{
        HeaderLogo
    },
    created(){
        this.changePage('Shop');
    },
    methods: {
        ...mapMutations(["changePage","changeCartStatus", "saveCartList", "deleteCartShop"]),
        scrollDown() {
            window.scrollBy({
                top: window.innerHeight,
                left: 0,
                behavior: "smooth",
            });
        },
        addCart(shop) {
            this.changeCartStatus(true);
            let {id,name,price} = shop;
            this.saveCartList({id,name,price,count:1});
        },
        openVisible(){
            this.dialogVisible = true;
        },
        shutVisible(){
            this.dialogVisible = false;
        }
    },
}
</script>

